/* App.css */

/* Contenedor principal de la aplicación */
.App {
  text-align: center;
  padding-top: 80px; /* Espacio para la navbar fija */
}

/* Encabezado (header) de la aplicación */
.App-header {
  background-color: #282c34;
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 20px;
}

/* Enlaces dentro de la aplicación */
.App-link {
  color: #61dafb;
  text-decoration: none;
  transition: color 0.3s ease;
}

.App-link:hover {
  color: #21a1f1;
}

/* Parrilla de habilidades */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr); /* Cambiado a 4 columnas */
  gap: 20px;
  margin-top: 40px; /* Espacio superior para la parrilla */
  margin-bottom: 40px; /* Espacio inferior para la parrilla */
}

/* Elementos de habilidades */
.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

/* Secciones generales */
section {
  margin: 60px 0; /* Espacio entre secciones */
  padding: 0 20px; /* Espacio lateral para las secciones */
}

/* Encabezados de secciones */
section h2 {
  margin-bottom: 30px; /* Espacio inferior para los encabezados */
  font-size: 2em; /* Tamaño de fuente para los encabezados */
}

/* Estilos para la imagen de perfil */
.profile-picture {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
  object-position: top; /* Ajusta la posición de la imagen dentro del contenedor */
  margin-bottom: 20px;
}

/* Estilos para la sección Hero */
.hero {
  position: relative;
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 100px 20px;
}

.hero-overlay {
  background: rgba(0, 0, 0, 0.5);
  padding: 50px;
  border-radius: 10px;
}

/* Estilos para la sección de proyectos */
.projects-grid, .experience-grid, .education-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 20px;
  margin-top: 40px;
}

.project-item, .experience-item, .education-item {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.project-item:hover, .experience-item:hover, .education-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.project-item img, .experience-item img, .education-item img {
  width: 100%;
  height: auto;
}

.project-info, .experience-info, .education-info {
  padding: 20px;
  text-align: left;
}

.project-info h3, .experience-info h3, .education-info h3 {
  margin-top: 0;
  font-size: 1.5em;
}

.project-info p, .experience-info p, .education-info p {
  margin: 10px 0;
}

.project-info .tech-stack, .experience-info .tech-stack, .education-info .tech-stack {
  font-weight: bold;
  color: #555;
}

/* Estilos para la sección About */
.about-content p {
  margin-bottom: 20px; /* Espacio inferior para los párrafos */
}

.contact-details {
  margin-top: 40px; /* Espacio superior para la información de contacto */
}

.cv-download {
  margin-top: 20px; /* Espacio superior para el enlace de descarga del CV */
}

/* Estilos para el botón de contacto en la sección About */
.contact-button {
  margin-top: 20px;
}

.contact-button .btn {
  display: inline-block;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.contact-button .btn:hover {
  background-color: #0056b3;
}

/* Estilos para el grupo de botones en la sección About */
.button-group {
  margin-top: 20px;
  display: flex;
  justify-content: center; /* Centrar los botones horizontalmente */
  gap: 10px; /* Espacio entre los botones */
}

.button-group .btn {
  display: inline-block;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  cursor: pointer;
}

.button-group .btn:hover {
  background-color: #0056b3;
}

/* Estilos para la sección Contact */
.contact-info {
  text-align: left; /* Alinear el texto a la izquierda */
  margin-left: auto;
  margin-right: auto;
  max-width: 600px; /* Ajusta el ancho máximo según sea necesario */
  background-color: #f9f9f9; /* Fondo claro para la sección */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-info p {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  font-size: 1.1em; /* Tamaño de fuente ligeramente mayor */
}

.contact-info p svg {
  margin-right: 10px; /* Espacio entre el icono y el texto */
}

.contact-info a {
  color: #007bff; /* Color del enlace */
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.contact-form {
  margin-top: 20px;
  max-width: 600px; /* Ajusta el ancho máximo según sea necesario */
  margin-left: auto;
  margin-right: auto;
  background-color: #f9f9f9; /* Fondo claro para el formulario */
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.contact-form input,
.contact-form textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.contact-form button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.contact-form button:hover {
  background-color: #0056b3;
}
